___CSS_LOADER_EXPORT___.push([module.id, `body {
    margin: 0;
    background: linear-gradient(160deg, #000000 20%, #188d52 100%);
    color: #fff;
    font-family: sans-serif;
    overflow: hidden;
    padding-bottom: 100px;
    overflow: hidden;
    background-attachment: fixed; 
  }
  
  .hub-container {
    margin-bottom: 100px;
    padding: 50px;
    gap: 20px;
    display: flex;
    flex-direction: column;
    transition: transform 0.4s ease;
    will-change: transform;
  }

  .hulu-header {
    width: 100%;
    height: 170px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #1ce783;
    font-weight: bold;
    font-size: 5rem;
  }

  .collection-title {
    font-size: 1.3rem;
    margin-bottom: 25px;

  }

  .tiles-wrapper {
    padding-top: 10px;
    padding-bottom: 13px;
    min-height: 200px;
    display: flex;
    gap: 40px;
    flex-wrap: nowrap;
    transition: transform 0.4s ease;
    will-change: transform;
  }
  
  .tile {
    width: 300px;
    flex-shrink: 0;
    border-radius: 8px;
    overflow: hidden;
    outline: none;
    transition: transform 0.3s ease;
    display: flex;
    flex-direction: column;
    position: relative;
  }
  
  .tile:focus {
    transform: scale(1.2);
    box-shadow: 0 0 0 4px #1ce783;
    z-index: 1;
    box-shadow: 0 0 10px 4px rgba(28, 231, 131, 0.6);
    transition: transform 0.25s ease, box-shadow 0.25s ease;
  }
  
  .tile:focus .tile-title {
    color: white;
  }
  
  .tile-image {
    border-radius: 6px;
    border-width: 1px;
    width: 100%;
    height: auto;
    display: block;
    object-fit: cover;
  }
  
  .tile-title {
    padding: 6px 6px;
    position: absolute;
    bottom: 0;
    left: 0;
    color: rgb(151, 151, 151);
    font-size: 1rem;
    pointer-events: none;
    transition: opacity 0.2s ease;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }


  .tile-logo-fallback {
    border-radius: 6px;
    border-width: 1px;
    width: 100%;
    height: 170px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #1ce783;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 8px;
    background-color: black;
   
  }
  
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.75);
    display: flex;    
    align-items: center;
    justify-content: center;
    z-index: 999;
  }

  .modal {
    position: relative;
    background-color: rgba(0, 0, 0, 0.85);
    background-blend-mode: darken;
    backdrop-filter: blur(6px);
    color: #fff;
    padding: 24px;
    border-radius: 10px;
    width: 600px;
    max-width: 90vw;
    max-height: 80vh;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 16px;
    min-height: 300px;
    opacity: 0;
    transform: scale(0.95);
    transition: opacity 0.25s ease, transform 0.25s ease;
    animation: pulseGlow 1.5s infinite ease-in-out;
  }
  
  .modal::before {
    content: "";
    position: absolute;
    inset: 0;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.85), transparent 60%);
    z-index: 1;
    pointer-events: none;
  }
  
  .modal * {
    z-index: 2;
    position: relative;
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.85);
  }

  .modal p {
    margin: 0;
    font-size: 1rem;
    color: #ccc;
    line-height: 1.5;
  }

  .modal-metadata {
    position: absolute;
    color: #ccc;
    font-size: 0.75rem;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8);
  }
  
  .modal-close {
    margin-top: 16px;
    padding: 8px 16px;
    background: linear-gradient(160deg, #000000 20%, #188d52 100%);
    border: none;
    border-radius: 6px;
    font-weight: bold;
    color: #ccc;
    align-self: flex-end;
    z-index: 2;
  }

  .modal-open {
    opacity: 1;
    transform: scale(1.5);
  }
  
  .modal-no-image {
    background-color: #222;
  }

  @keyframes pulseGlow {
    0% { box-shadow: 0 0 0 0 rgba(28, 231, 131, 0.3); }
    70% { box-shadow: 0 0 0 15px rgba(28, 231, 131, 0); }
    100% { box-shadow: 0 0 0 0 rgba(28, 231, 131, 0); }
  }

